import axios from 'axios';
async function GetUsers(arg){
    try{
        let url = 'https://getpantry.cloud/apiv1/pantry/48772c43-095e-44c0-b987-edab68e5bbda/basket/usersDate';
        let response = await axios.get(url);
        console.log(response.data.usersDate)
        if(response.data.usersDate) arg(response.data.usersDate)
    }catch(e){
        console.log(e);
    }
}
export default GetUsers