
import { useEffect, useState } from 'react';
import Bio from '../Bio/Bio';
import React from 'react';
import Context from '../Context/Context';
import getUsers from '../hooks/getUsers';





function Login() {
  let sizes = [300, 500, 700, 1000, 1600, 2560];
  let [screenWidth, setScreenWidth] = useState(window.screen.width)
  let [currentUser, setCurrentUser] = React.useState([])
  let [mobileBio, setMobileBio] = useState('none')
  let [currentPerson, setCurrentPerson] = useState({})
  let [isTrade, setIsTrade] = useState(false)
  let [isPosts, setIsPosts] = React.useState(true)
  let [postsDate, setPostsDate] = React.useState([])
  let [isContacts, setIsContacts] = React.useState(false)
  let [currentItem, setCurrentItem] = useState({loging: '', hiden: ''})
  let [usersDate, setUsersDate] = React.useState([{
    email: 'aaa',
    password: 'aaa',
    name: 'Amilia Luna',
    img: "imgs/girl.png",
    id: '0001',
    friends: ['0002'],
    treads: {}
}])
let aaa = window.screen.width
window.addEventListener('resize', (e)=>{
  sizes.forEach(item=>{
    if(e.target.innerWidth - 3 <= item && e.target.innerWidth + 3 >= item){
      console.log(e.target.innerWidth);
      setScreenWidth(item)
    }
  })

  
})


  useEffect(()=>{
    getUsers(setUsersDate)
  }, [])


  return (
    <>

      <Context.Provider value={{setCurrentUser, currentUser, setUsersDate, usersDate, setPostsDate, setIsPosts, setCurrentItem, currentItem, currentPerson, setIsContacts, setIsTrade, mobileBio, setMobileBio, setCurrentPerson, screenWidth}}>
        <Bio usersDate={usersDate}/>
      </Context.Provider>
          </>
  );
}

export default Login;
