import { useContext } from "react"
import Context from "../../Context/Context"
import Post from "./Post";
import scrolling from "../../hooks/scrolling";
import { useEffect } from "react";
import './news.css';


function News(props){
    const {postsDate, isPosts} = useContext(Context)

    useEffect(()=>{
        scrolling(props.news, window.screen.width)
      
}, [isPosts])
    if(isPosts && postsDate[0]){
    return(
        <div className="news_main">
        {postsDate.map(item=>{
            return(
            item.map(item=>{
                return(
                    
                <Post key={Math.random()} item={item}/>
                
                )
            })
            )
        })}
    </div>
    )
    }
    else{
        return(
            <div className="news_main">
                <h1>Loading...</h1>
                </div>)
    }
        
    
}
export default News