import './friends.css'
import {useDispatch} from 'react-redux';

function Friend(item, func){
    let dispatch = useDispatch()
    return(
        <div className="friend_item">
                <div className="is_online online_true"></div>
                <img src={require(`../../${item.item.img}`)} onClick={()=> dispatch({type:'SET_C_P', item: item.item})} id={item.item.id} className="friend_img" alt=""/>
                <p className="friend_name c_hover">{item.item.name}</p>
            </div>
    )
}
export default Friend